import { STORAGE_KEYS } from './../config/core/storage-keys.config';
import { FieldMessage } from './../models/config/field-message';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { StorageService } from './storage.service';
import { Injectable } from '@angular/core';

export class TrataErrorService {

  constructor() { }

  static tratarError(error: any): any {

    let errorObj = error;
    if (errorObj.error) {
      errorObj = errorObj.error;
    }
    if (!errorObj.status) {
      errorObj = JSON.parse(errorObj);
    }
    switch (errorObj.status) {
      case 401:
        this.handle401();
        break;
      case 403:
        this.handle403();
        break;
      case 422:
        EventEmitterService.getError422().emit(errorObj.errors);
        break;
      default:
        this.handleDefaultEror(errorObj);
    }

  }

  private static handle401() {
    EventEmitterService.emitirMensagemToasterDanger("Login", "Erro: 401 - Messagem: Email ou senha incorreto");
  }
  private static handle403() {
    localStorage.removeItem(STORAGE_KEYS.localUser);
  }

  private static handle422(errorObj) {
    return errorObj.errors;
  }

  private static handleDefaultEror(errorObj) {
    EventEmitterService.emitirMensagemToasterAlerta("Alerta", `Erro: ${errorObj.status} - Messagem: ${errorObj.message}`);
  }
}


